.link {
  text-decoration: none;
}

.link--breadcrumb{
  vertical-align: top;

  font-weight: 700;
  font-size: 12px;
  line-height: 17px;

  color: $text-white;
  text-transform: uppercase;

  letter-spacing: 0.02em;
  font-feature-settings: 'pnum' on, 'lnum' on;

  padding-left: 14px;

  position: relative;

  &:hover {
    opacity: 0.7;
  }
}

.link--breadcrumb::before {
  content: "";
  position: absolute;
  background-image: url("../img/arrow-icon.svg");
  left: 0;
  top: 1px;
  width: 6px;
  height: 10px;
  background-size: cover;
  background-repeat: no-repeat;;
}


.link--underline {
  color: $white;
  line-height: 40px;
  font-size: 15px;

  position: relative;

  border-bottom: 2px solid #F2890F;
}

.link--underline::before {
  content: ''; 
    position: absolute; 
    right: -4px;
    bottom: calc(50% - 6px);
    border: 4px solid transparent;
    border-top: 5px solid $white;
    transform: rotate(270deg);
}